
























































































































  import {Component, Vue} from "vue-property-decorator";
  import jobApi from "@/api/jobApi";
  import jobInstanceApi from "@/api/jobInstanceApi";
  import {IJob} from "@/model/jobModel";
  import {IJobInstance} from "@/model/jobInstanceModel";

  @Component
export default class JobDetailCustom extends Vue {
  rt: any = {
    jobId: ""
  };
  job: IJob = {
    config: {}
  };
  // Job Instance List
  jobInstanceListColumns: object[] = [
    {
      type: "expand",
      key: "id",
      width: 50
    },
    {
      title: "ID",
      key: "id",
      align: "center",
      width: 100
    },
    {
      title: "名称",
      key: "name",
      align: "center",
      slot: "name"
    },
    {
      title: "类型",
      key: "type",
      align: "center",
      slot: "type"
    },
    {
      title: "创建时间",
      key: "createTime",
      align: "center"
    },
    {
      title: "开始时间",
      key: "startTime",
      align: "center"
    },
    {
      title: "结束时间",
      key: "stopTime",
      align: "center"
    },
    {
      title: "状态",
      key: "status",
      align: "center"
    },
    {
      title: "操作",
      fixed: "right",
      align: "center",
      slot: "operator",
      width: 100
    }
  ];
  jobInstanceList: IJobInstance[] = [];
  clickStart() {
    jobApi
      .startJob({ jobId: this.rt.jobId })
      .then(res => {
        this.$Notice.success({ title: "启动作业成功" });
      })
      .catch(res => {
        this.$Notice.warning({
          title: "启动作业失败",
          desc: res.msg
        });
      });
  }
  clickRestart() {
    jobApi
      .restartJob({ jobId: this.rt.jobId })
      .then(res => {
        this.$Notice.success({ title: "重启作业成功" });
      })
      .catch(res => {
        this.$Notice.warning({
          title: "重启作业失败",
          desc: res.msg
        });
      });
  }
  clickStop() {
    jobApi
      .stopJob({ jobId: this.rt.jobId })
      .then(res => {
        this.$Notice.success({ title: "停止作业成功" });
      })
      .catch(res => {
        this.$Notice.warning({
          title: "停止作业失败",
          desc: res.msg
        });
      });
  }
  clickDelete() {
    jobApi
      .deleteJob({ jobId: this.rt.jobId })
      .then(res => {
        this.$Notice.success({ title: "删除作业成功" });
        this.$router.push({
          path: "/page/job/list"
        });
      })
      .catch(res => {
        this.$Notice.warning({
          title: "删除作业失败",
          desc: res.msg
        });
      });
  }
  clickEdit() {
    this.$router.push({
      path: "/page/job/edit",
      query: {
        id: this.rt.jobId
      }
    });
  }
  // get
  getJob() {
    jobApi
      .queryJob({ jobId: this.rt.jobId })
      .then((res: any) => {
        this.job = res;
      })
      .catch(res => {
        this.$Notice.error({ title: res.msg });
      });
  }

  getJobInstanceList() {
    jobInstanceApi
      .queryJobInstances({ jobId: this.rt.jobId })
      .then((res: any) => {
        this.jobInstanceList = res.list;
      });
  }

  parseRouter() {
    this.rt.jobId = this.$route.query.id;
  }

  mounted() {
    this.parseRouter();
    this.getJob();
    this.getJobInstanceList();
  }
}
